}

:root {
    --light: #ffffff;
    --pLight: #5f5b5a;
    --primary: #3e3b3a;
    --pDark: #262626;
    --dark: #000000;
    font-size: 1vw;
}

a{
    text-decoration: none;
}

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    line-height: 140%;
}

body{
    font-family: 'Source Code Pro', monospace;
    font-weight: normal;
    color: #000000;
    color: var(--dark);
    background: #262626;
    background: var(--pDark);
}

html, body{
    max-width: 100vw;
    overflow-x: hidden;
    height: auto!important;
}

.simplebar-track{
    background: #ffffff;
    background: var(--light);
    overflow-x: hidden;
}

.simplebar-scrollbar::before{
    background-color: #3e3b3a!important;
    background-color: var(--primary)!important;
    opacity: 1!important;
    border-radius: 9999vw 0 0 9999vw!important;
    right: 0!important;
    left: 40%!important;
}

.simplebar-hover::before, .simplebar-dragging::before{
    background-color: #3e3b3a!important;
    background-color: var(--primary)!important;
    left: 20%!important;
}

.simplebar-track.simplebar-horizontal {
    display: none!important;
}

@media only screen and (max-width: 767px) {
    html{
        overflow-y: hidden;
    }
}

#loadingMain{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: var(--dark);
    animation: showPage 1.8s ease forwards 6s reverse;
    z-index: 9999;
}

#loadingName{
    width: 60vw;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    fill: transparent;
    -webkit-animation: fillAnim 1s ease forwards 5s;
            animation: fillAnim 1s ease forwards 5s;
}

#page{
    display: none;
}

.pageShown{
    -webkit-animation: showPage 2.5s ease forwards;
            animation: showPage 2.5s ease forwards;
}

#loadingName > path{
    white-space: pre;
    stroke: rgb(255, 255, 255);
    stroke-width: 5px;
