@-webkit-keyframes lineAnim {
    to{
        stroke-dashoffset: 0;
    }
}

@keyframes lineAnim {
    to{
        stroke-dashoffset: 0;
    }
}

@-webkit-keyframes fillAnim{
    from{
        fill: transparent;
    }
    to{
        fill: rgb(255, 255, 255);
    }
}

