:root{
    --iconColor: var(--light);
    --iconSize: 3rem;
    --textIconSize: 3rem;
    --textIconMargin: 0rem;
    --iconMargin: 0rem;
    --lineWidth: .35rem;
}

.button{
    all: unset;
    text-transform: none;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
    padding: .5vw;
    font-size: 1.2rem;
    cursor: pointer;
    color: var(--pDark);
    background: transparent;
    border-radius: .25rem;
    transition: color;
}

.header.button{
    padding: 1vw;
}

.leftLine {
    border: none;
    border-left: .35rem solid var(--pDark);
    border-left: var(--lineWidth) solid var(--pDark);
}

.rightLine {
    border: none;
    border-right: .35rem solid var(--pDark);
    border-right: var(--lineWidth) solid var(--pDark);
}

.bottomLine {
    border: none;
    border-bottom: .35rem solid var(--pDark);
    border-bottom: var(--lineWidth) solid var(--pDark);
}

.topLine {
    border: none;
    border-top: .35rem solid var(--pDark);
    border-top: var(--lineWidth) solid var(--pDark);
}

:root .button{
    --iconColor: var(--pDark);
}

.invert{
    color: var(--light);
}

.invert.leftLine {
    border: none;
    border-left: .35rem solid var(--light);
    border-left: var(--lineWidth) solid var(--light);
}

.invert.rightLine {
    border: none;
    border-right: .35rem solid var(--light);
    border-right: var(--lineWidth) solid var(--light);
}

.invert.bottomLine {
    border: none;
    border-bottom: .35rem solid var(--light);
    border-bottom: var(--lineWidth) solid var(--light);
}

.invert.topLine {
    border: none;
    border-top: .35rem solid var(--light);
    border-top: var(--lineWidth) solid var(--light);
}


:root .invert{
    --iconColor: var(--light);
}

.button *{
    z-index: 3;
    transition: .5s;
}

.button > span{
    -webkit-transition: 0s;
}

.button:hover, .button:focus {
    --iconColor: var(--light);
    background: transparent;
    color: var(--light);
}

.invert:hover, .invert:focus {
    --iconColor: var(--pDark);
    color: var(--pDark);
}

.leftLine.button:hover:before, .leftLine.button:focus:before, .leftLine.invert:hover:before, .leftLine.invert:focus:before{
    right:0;
}

.rightLine.button:hover:before, .rightLine.button:focus:before, .rightLine.invert:hover:before, .rightLine.invert:focus:before{
    left:0;
}

.topLine.button:hover:before, .topLine.button:focus:before, .topLine.invert:hover:before, .topLine.invert:focus:before{
    bottom:0;
}

.bottomLine.button:hover:before, .bottomLine.button:focus:before, .bottomLine.invert:hover:before, .bottomLine.invert:focus:before{
    top:0;
}

.button:before {
    content: "";
    position: absolute;
    width: 110%;
    height: 100%;
    background: var(--pDark);
    transition: .5s;
    z-index: 2;
}

.invert:before {
    background: var(--light);
}

.leftLine.button:before{
    right: 100%;
}

.rightLine.button:before{
    left: 100%;
}

.topLine.button:before{
    bottom: 100%;
}

.bottomLine.button:before{
    top: 100%;
}

.headerHeader{
    all: unset;
